import React, { useEffect, useState } from "react";
import { useParams } from "react-router";
import portfolioData from "../components/Data";

function PortfolioItem() {
    const { slug } = useParams();
    const [item, setItem] = useState();
    useEffect(() => {
        window.scrollTo({
            top: 0,
            behavior: "smooth",
        });
        if (slug) {
            setItem(portfolioData.find((item) => item?.slug === slug));
        }
    }, [slug]);

    return (
        <div className="container my-5">
            <h1 className="text-center display-2 py-4">{item?.text}</h1>
            <div className="row  row-cols-md-2 row-cols-lg-3 mt-5">
                <div
                    className="my-2 m-lg-0 d-flex align-items-center fw-light ff-apple text-gray"
                    style={{ fontSize: "16px" }}
                >
                    <i class="bi bi-dot fs-4" style={{ color: "#C15723" }}></i>
                    <div className="text-center">
                        Местоположение: {item?.additionalData?.placement}
                    </div>
                </div>
                <div
                    className="my-2 m-lg-0 d-flex align-items-center fw-light ff-apple text-gray"
                    style={{ fontSize: "16px" }}
                >
                    <i class="bi bi-dot fs-4" style={{ color: "#C15723" }}></i>
                    <div className="text-center">
                        Площадь объекта: {item?.additionalData?.plosad}
                    </div>
                </div>
                <div
                    className="my-2 m-lg-0 d-flex align-items-center fw-light ff-apple text-gray"
                    style={{ fontSize: "16px" }}
                >
                    <i class="bi bi-dot fs-4" style={{ color: "#C15723" }}></i>
                    <div className="text-center">
                        Стиль интерьера: {item?.additionalData?.style}
                    </div>
                </div>
            </div>
            <hr className="my-4" />
            {item?.html}
        </div>
    );
}

export default PortfolioItem;
