import React, { useEffect } from "react";
import { useDispatch } from "react-redux";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "./App.css";
import Layout from "./layouts/Layout";
import { mainRoutes, authRoutes } from "./routes";
import { checkAuthenticated, load_user } from "./redux/actions/auth";
import WithLoged from "./layouts/hocs/withLoged";
import AuthLayout from "./layouts/AuthLayout";
import WithAuth from "./layouts/hocs/withAuth";

function App() {
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(checkAuthenticated()).then((res) => {
            if (res === 200) {
                dispatch(load_user());
            }
        });
    }, []);
    return (
        <Router basename="/geometrium">
            <Switch>
                {authRoutes.map((route) => (
                    <Route {...route}>
                        <WithLoged>
                            <AuthLayout>{route.comp}</AuthLayout>
                        </WithLoged>
                    </Route>
                ))}
                {mainRoutes.map((route) => (
                    <Route key={route.path} {...route}>
                        {/* <WithAuth> */}
                        <Layout>{route.comp}</Layout>
                        {/* </WithAuth> */}
                    </Route>
                ))}
            </Switch>
        </Router>
    );
}

export default App;
