import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { login } from "../redux/actions/auth";
import errors from "../redux/messages/errors.json";

function Login() {
    const [formData, setFormData] = useState({
        username: "",
        password: "",
    });
    const [isLoading, setIsLoading] = useState(false);
    const [resErrors, setErrors] = useState(null);
    const { username, password } = formData;
    const inputChange = (e) =>
        setFormData({ ...formData, [e.target.name]: e.target.value });
    useEffect(() => {
        return () => {
            setFormData({
                username: "",
                password: "",
            });
        };
    }, []);
    // redux
    const dispatch = useDispatch();

    // submit
    const submitHandler = (e) => {
        e.preventDefault();
        setIsLoading(true);
        dispatch(login(username, password)).then((res) => {
            setIsLoading(false);
            console.log("RESULT", res);
            if (res === errors.error4) {
                // setErrors(res);
                setErrors("Нет такого пользователя или неверный пароль");
            } else {
                setErrors(null);
            }
        });
    };

    return (
        <div className="container">
            <div className="d-flex mt-5 flex-column py-3 align-items-start justify-content-md-center align-items-md-center">
                <Link to="/" className="mx-md-0 mt-5 mb-3 ps-4 ps-md-0">
                    <img
                        style={{ width: "250px", height: "34px" }}
                        src={process.env.PUBLIC_URL + "/images/logo.svg"}
                        alt="Logo"
                    />
                </Link>
                {/* <div
                    className="h2 py-2 ps-4 ps-md-0 text-black"
                    style={{ fontWeight: 500, fontSize: "32px" }}
                >
                    Войти
                </div> */}
                {/* <div className="p ps-4 ps-md-0">
                    Следите за обновлениями в своей профессиональной сети
                </div> */}

                <form
                    onSubmit={(e) => submitHandler(e)}
                    className="bg-white p-4 col-md-8 col-lg-4 my-4 shadow text-black"
                    style={{ borderRadius: "10px" }}
                >
                    {resErrors && (
                        <div className="mb-4 text-danger text-center">
                            <strong>{resErrors} !</strong>
                        </div>
                    )}
                    <div className="mb-4">
                        <label
                            htmlFor="exampleInputEmail1"
                            className="form-label text-gray py-0 fw-bold"
                            style={{ fontSize: "14px" }}
                        >
                            <i className="bi bi-person-fill fs-6 me-2"></i>
                            Ваше имя <small>(логин)</small>
                        </label>
                        <input
                            type="text"
                            name="username"
                            id="loginUsername"
                            value={username}
                            placeholder="Например: adidas"
                            onChange={(e) => inputChange(e)}
                            required
                            className={`form-control sm-placeholder ${
                                resErrors ? "is-invalid" : ""
                            }`}
                            style={{ borderRadius: "7px" }}
                            aria-describedby="emailHelp"
                        />
                    </div>
                    <div className="mb-3 has-validation">
                        <label
                            htmlFor="exampleInputPassword1"
                            className="form-label text-gray fw-bold py-0 "
                            style={{ fontSize: "14px" }}
                        >
                            <i className="bi bi-unlock me-2 fs-6"></i>
                            Пароль
                        </label>
                        <input
                            type="password"
                            className={`form-control sm-placeholder ${
                                resErrors ? "is-invalid" : ""
                            }`}
                            style={{ borderRadius: "7px" }}
                            name="password"
                            value={password}
                            placeholder="Ваш секретный пароль"
                            id="loginPassword"
                            onChange={(e) => inputChange(e)}
                            required
                            onKeyDown={(e) => {
                                if (e.key === "Enter") {
                                    submitHandler(e);
                                }
                            }}
                        />
                    </div>

                    <button
                        type="submit"
                        style={{ paddingTop: "10px", paddingBottom: "10px" }}
                        className="btn black-btn px-5 mt-3 fw-bold my-2 text-center w-100"
                    >
                        {isLoading ? (
                            <div
                                className="spinner-border text-white"
                                role="status"
                            >
                                <span className="visually-hidden">
                                    Loading...
                                </span>
                            </div>
                        ) : (
                            <>Войти</>
                        )}
                    </button>
                </form>
                {/* <ShortFooter /> */}
            </div>
        </div>
    );
}

export default Login;
