import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import portfolioData from "../components/Data";
import DataPagination from "../components/DataPagination";
import StandartDropSelect from "../components/StandartDropSelect";

function Portfolio() {
    useEffect(() => {
        window.scrollTo({
            top: 0,
            behavior: "smooth",
        });
    }, []);

    const [filterData, setFilterData] = useState({
        category: "0",
    });

    const filterStates = [
        { name: "Премиум", value: "premium" },
        { name: "Бизнес класс", value: "businessclass" },
    ];
    const filterTypes = [
        { name: "Все объекты", value: "0" },
        { name: "Дома и виллы", value: "1" },
        { name: "Квартиры", value: "2" },
        { name: "Коммерческие интерьеры", value: "3" },
        { name: "Архитектура", value: "4" },
    ];
    const [resData, setResData] = useState([]);
    useEffect(() => {
        let nedata = portfolioData;

        if (filterData?.category !== "0") {
            nedata = nedata.filter(
                (item) => item?.category === filterData?.category
            );
        }
        if (filterData?.state) {
            nedata = nedata.filter(
                (item) => item?.filter === filterData?.state
            );
        }
        setResData(nedata);
        if (filterData?.category === "0" && !filterData?.state) {
            setResData(portfolioData);
        }
    }, [filterData]);

    return (
        <div className="mt-5 pt-4">
            <div className="container ">
                <h5>Портфолио студии</h5>
                <div className="row align-items-center justify-content-between mt-4 mb-5">
                    <div className="col-lg-2">
                        <StandartDropSelect
                            name="Без фильтра"
                            noSearch={true}
                            clickFun={(value) =>
                                setFilterData({ ...filterData, state: value })
                            }
                            options={filterStates}
                            resetFun={() => {
                                setFilterData({ ...filterData, state: null });
                            }}
                            // full_name={"Состояние"}
                        />
                    </div>
                    <div className="col-md-12 col-lg-10 row">
                        {filterTypes.map((item) => (
                            <div
                                onClick={() =>
                                    setFilterData({
                                        ...filterData,
                                        category: item?.value,
                                    })
                                }
                                className={`filteritem col-auto mt-2 ${
                                    filterData?.category === item?.value
                                        ? "active"
                                        : ""
                                }`}
                            >
                                {item?.name}
                            </div>
                        ))}
                    </div>
                </div>
            </div>
            <div className="px-2 mb-5">
                <div className="row m-0">
                    {resData.map((item) => (
                        <div className="col-lg-4 mb-lg-0 mb-4 px-1">
                            <Link to={`/portfolio/${item?.slug}/`}>
                                <div
                                    className="position-relative projectitem mb-2"
                                    role="button"
                                >
                                    <img
                                        src={item?.imgsrc}
                                        className="img-fluid"
                                        style={{ height: "300px" }}
                                        alt=""
                                    />
                                    <div
                                        className="position-absolute"
                                        style={{
                                            top: 0,
                                            padding: 0,
                                            margin: 0,
                                            display: "none",
                                            height: "100%",
                                            width: "100%",
                                            backgroundColor: "rgba(0,0,0,0.5)",
                                        }}
                                    >
                                        <div
                                            className="position-absolute d-flex justify-content-end"
                                            style={{
                                                bottom: "70px",
                                                right: "10px",
                                                borderBottom:
                                                    "3px solid #C15723",
                                                width: "50%",
                                            }}
                                        >
                                            <h6
                                                className="text-white"
                                                style={{ fontSize: "16px" }}
                                            >
                                                {item?.text}
                                            </h6>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        </div>
                    ))}
                </div>
            </div>
            <DataPagination />
        </div>
    );
}

export default Portfolio;
